import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '*',
            redirect: '/index'
        },
        {
            path: '/home',
            name: 'home',
            component: resolve=>(require(["@/components/Master.vue"],resolve)),
            redirect:'/index',
            children:[
                {
                    path: '/index',
                    name: 'index',
                    component: resolve=>(require(["@/components/deveGuide/Index.vue"],resolve))
                },
                {
                    path: '/quickStart',
                    name: 'quickStart',
                    component: resolve=>(require(["@/components/deveGuide/QuickStart.vue"],resolve))
                },
                {
                    path: '/codeStandard',
                    name: 'codeStandard',
                    component: resolve=>(require(["@/components/deveGuide/CodeStandard.vue"],resolve))
                },
                {
                    path: '/button',
                    name: 'button',
                    component: resolve=>(require(["@/components/base/Button.vue"],resolve))
                },
            ]
        },

    ]
})



